@import url('https://fonts.googleapis.com/css2?family=Fira+Code:wght@300&family=Inter:wght@200;400;500;600&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Oswald:wght@300;400;500&display=swap');

*,
*::after,
*::before {
	box-sizing: border-box;
	margin: 0;
	padding: 0;
}

:root {
	--navy: rgb(13, 33, 64);
	--navy-light: rgb(16, 43, 85);
	--blue: rgb(98, 161, 255);
	--blue-light: rgba(98, 161, 255, 0.2);
	--white: rgb(204, 214, 246);
	--light-white: rgb(207, 219, 255, 0.7);
}

html,
body {
	width: 100vw;
	max-width: 100vw;
	overflow-x: hidden;
	background: var(--navy);
	-webkit-font-smoothing: antialiased;
	font-family: 'Inter', 'San Francisco', 'SF Pro Text', -apple-system, system-ui, sans-serif;
}

a {
	text-decoration: none;
}

p {
	line-height: 32px;
}

button {
	font-family: 'Inter', 'San Francisco', 'SF Pro Text', -apple-system, system-ui, sans-serif;
}
